import { Component, OnInit } from '@angular/core';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject, forkJoin } from 'rxjs';
import { first, map } from 'rxjs/operators';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model Device
import { ACL } from '../../../../../partials/layout/tacgui/_forms/tac-acl-form/acl.model'
//Service//
import { ACLService } from '../acl.service'

@Component({
  selector: 'kt-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

  validation = new BehaviorSubject({custom:[]})
  loadingForm = new BehaviorSubject(true)

  formParameters = {
    action: 'add',
    data: new ACL,
    old_data: new ACL
  }

  constructor(
    private toastr:ToastrService,
    private aclservice: ACLService,
    private router:Router,
    private route: ActivatedRoute
  ) {

  }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
    this.route.paramMap.pipe(
      first()
    ).subscribe( params => {
      this.fillForm(+params.get('id'))
    });
  }

  fillForm(id:number){
    console.log(id)
    this.aclservice.get(id).subscribe( data => {
      console.log(data)
      if (!data){
        this.toastr.error('Server Error!')
        this.router.navigate(['../../'], { relativeTo: this.route });
        return
      }

      Object.assign(this.formParameters.data, this.makeClone(data))
      Object.assign(this.formParameters.old_data, this.makeClone(data))

      this.loadingForm.next(false)

    })
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  save(){
    this.loadingForm.next(true)
    let v_custom = {}
    let data = this.makeClone(this.formParameters.data)
    let old_data = this.makeClone(this.formParameters.old_data)

    console.log(JSON.stringify(this.formParameters.data), JSON.stringify(this.formParameters.old_data))
    if (JSON.stringify(data) == JSON.stringify(old_data)){
      this.toastr.warning('No Changes Detected!')
      this.loadingForm.next(false)
      return;
    }

    data.ace = ( data.ace[0] ) ? data.ace.map( (x,i) => {
      v_custom[i] = {}
      x.nac = (x.nac[0]) ? x.nac[0].id : null
      if (!x.nac) v_custom[i].nac = 'NAC empty!'
      x.nas = (x.nas[0]) ? x.nas[0].id : null
      if (!x.nas) v_custom[i].nas = 'NAS empty!'
      x.order = i+1
      return x
    }) : []

    // console.log(data)

    this.aclservice.save(data).subscribe( data => {
      console.log(data)
      if (data.error.validation) data.error.validation.custom = v_custom;
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.save) {
        this.toastr.success('ACL Changed!');
        this.router.navigate(['../../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
