import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
//CDK
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-tac-acl-form',
  templateUrl: './tac-acl-form.component.html',
  styleUrls: ['./tac-acl-form.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TacAclFormComponent implements OnInit {

  @Input() options

  @Input() validation
  @Input() loading

  objectKeys = Object.keys;

  list = {
    nas: {
      multiple: false,
      title: '',
      title_sidebar: 'Address List',
      search: true,
      apiurl: 'api/obj/address/list/',
      addNew: 'address'
    },
    nac: {
      multiple: false,
      title: '',
      title_sidebar: 'Address List',
      search: true,
      apiurl: 'api/obj/address/list/',
      addNew: 'address'
    },
  }

  constructor(private toastr:ToastrService) { }

  ngOnInit() {
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if ( key == 'ace') {
          this.toastr.error('ACE Error!');
          continue;
        }
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  cloneAce(index){
    this.options.data.ace.splice(index+1, 0, this.makeClone(this.options.data.ace[index]))
  }
  delAce(index){
    if ( this.options.data.ace.length == 1 ) return
    this.options.data.ace.splice(index, 1)
  }

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.options.data.ace, event.previousIndex, event.currentIndex);
  }

  setNas(data, index) {
    // console.log(data, index)
    this.options.data.ace[index].nas = data
  }
  setNac(data, index) {
    // console.log(data, index)
    this.options.data.ace[index].nac = data
  }

}
